interface ItemProps {
  data: Mv;
  unixTime: number;
}
const Item: React.FC<ItemProps> = ({ data, unixTime }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const handleClickMv = () => {
   if (data.stype === 'mv') {
    navigate(`/app/Video?uuid=${data.info.uuid}`);
   } else {
    navigate(`/app/comic`, { state:{ uuid: data.info.uuid } });
   }
  }
  return (
    <div className="rounded p-4 bg-background mb-4 text-sm" onClick={handleClickMv}>
      <div className="mb-2">{unixtime2Datetime(unixTime)}</div>
      <div className="grid grid-cols-[100px_1fr] mb-1">
        <span>{t("觀看作品")}</span>
        <span className="min-h-20 line-clamp-4">{data.info.name}</span>
      </div>
      <div className="grid grid-cols-[100px_1fr] mb-1">
        <span>{t("作品類型")}</span>
        <span>{data.pt_name}</span>
      </div>
      <div className="grid grid-cols-[100px_1fr]">
        <span>{t("到期日")}</span>
        <span>{data.exp}</span>
      </div>
    </div>
  );
};

interface ItemListProps {
  data: MyList;
}

const ItemList: React.FC<ItemListProps> = ({ data }) => (
  <div className="p-4">
    {Object.entries(data).map(([key, value], index) => (
      <Item key={index} data={value} unixTime={Number(key)}/>
    ))}
  </div>
);

const TabContent: React.FC = () => {
  const dispatch:AppDispatch = useDispatch();
  const myList = useSelector((state:RootState) => state.userInfo.myList);
  const onlineList = myList !== null 
  ? Object.entries(myList)
    .filter(([_, value]) => value.type === 'online') // 篩選 type 為 'online'
    .reduce((acc, [key, value]) => {
      acc[key] = value;
      return acc;
    }, {} as typeof myList) 
  : {};

  const dwList = myList !== null 
  ? Object.entries(myList)
    .filter(([_, value]) => value.type === 'dw') // 篩選 type 為 'dw'
    .reduce((acc, [key, value]) => {
      acc[key] = value;
      return acc;
    }, {} as typeof myList)
  : {};

  const { t } = useTranslation();
  useEffect(() => {
    dispatch(fetchMvList());
  }, [dispatch]);

  useEffect(() => {
    console.log(myList);
  }, [myList]);
  const tabContent = [
    {
      key: "線上觀看",
      label: t("線上觀看"),
      content: <ItemList data={onlineList} />,
    },
    {
      key: "下載收藏",
      label: t("下載收藏"),
      content: <ItemList data={dwList} />,
    },
  ];

  return <BasicTabs tabs={tabContent} />;
};

export default TabContent;
